import MinRouter from './config/MinRouter'

// 配置路由
 // type必须是以下的值['navigateTo', 'switchTab', 'reLaunch', 'redirectTo']
		  // 跳转方式(默认跳转方式)
const router = new MinRouter({
	routes: [
    {
    	path: 'pages/driver/index/index',
    	type: 'switchTab',
    	name: 'driveIndex'
    },
		{
			path: 'pages/driver/user/user',
		 
			type: 'switchTab',
			name: 'driverUser'
		},
    {
    	path: 'pages/login/login',
    	type: 'navigateTo',
    	name: 'login'
    },
    {
    	path: 'pages/driver/barCode/barCode',
    	type: 'navigateTo',
    	name: 'barCode'
    },
    {
    	path: 'pages/driver/petrol/petrolList',
    	type: 'switchTab',
    	name: 'petrolList'
    },
    {
    	path: 'pages/driver/transaction/transactionlist',
    	type: 'navigateTo',
    	name: 'transactionlist'
    },
    {
    	path: 'pages/driver/order/order',
    	type: 'navigateTo',
    	name: 'order'
    },
    {
      path: "pages/driver/order/orderPaySuccess",
      type: 'navigateTo',
      name: 'orderPaySuccess'
    }
	]
})

export default router
